// Variable Overrides

$theme-colors: (
      "primary":    #bf6567,
      "secondary":  #ffa270,
      //"success":    $success,
      //"info":       $info,
      //"warning":    $warning,
      //"danger":     $danger,
      "light":      #D6D6D6,
      "dark":       #222222
);

//Mixins

@mixin unselectable() {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

nav > *:focus, nav > *:active {
  outline: none !important;
  border: none !important;
}

#navbar-toggler:focus, #navbar-toggler:hover, #navbar-toggler:active {
  background-color: map-get($theme-colors, "dark") !important;
}

.navbar-toggler:focus {
  background: initial;
}

.nav-item {
  left: 2em;
  padding-right: 2em;
}

.nav-link {
    //color: map-get($theme-colors, "primary") !important;
    color: #fff !important;
    opacity: .6 !important;
}

.navbar-brand {
  font-family: "Caviar Dreams";
  font-size: 20pt !important;

}

.nav-link:hover {
    opacity: 1 !important;
    text-decoration: underline !important;
}

body {
  //background: url("../assets/Background.png");
  background-color: #D6D6D6 !important;
}

html, body {
  overflow-x:hidden;
}

svg {
  @include unselectable();
  pointer-events: none;
}

