//*********************
//Contact Form
//*********************
//Reset some styles from bootstrap
textarea:focus,
textarea.form-control:focus,
input.form-control:focus,
input[type=text]:focus,
input[type=password]:focus,
input[type=email]:focus,
input[type=number]:focus,
[type=text].form-control:focus,
[type=password].form-control:focus,
[type=email].form-control:focus,
[type=tel].form-control:focus,
[contenteditable].form-control:focus {
  background-color: initial;
  -webkit-box-shadow: initial;
  -moz-box-shadow: initial;
  box-shadow: none !important;
  outline: 0 !important;
}

.form-control {
  background-color: initial;
}

.form-label-group input,
.form-label-group textarea{
  border-bottom: solid 2px map_get($theme-colors, dark);
  color: map_get($theme-colors, dark);
}
.form-label-group input:focus,
.form-label-group textarea:focus {
  border-bottom: solid 2px map_get($theme-colors, primary);
}

.form-control{
  border: none;
}

.btn-primary:hover,.btn-primary:focus,.btn-primary:active,
.btn-primary:active:focus:not(:disabled):not(.disabled),
.btn:focus, .btn:active, .btn:hover{
  box-shadow: none!important;
  outline: 0;
}

label {
  @include unselectable();
}

//Floating Labels for Form
:root {
  --input-padding-x: .75rem;
  --input-padding-y: .75rem;
}

.form-control:focus {
  color: map_get($theme-colors, dark) !important;
}

.form-label-group {
  position: relative;
  margin-bottom: 1rem;
}

.form-label-group > input,
.form-label-group > textarea,
.form-label-group > label {
  padding: var(--input-padding-y) var(--input-padding-x);
  cursor: text !important;
}

.form-label-group > label {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  margin-bottom: 0; /* Override default `<label>` margin */
  line-height: 1.5;
  color: #495057;
  border: 1px solid transparent;
  border-radius: .25rem;
  -webkit-transition: all .1s ease-in-out;
  -moz-transition: all .1s ease-in-out;
  -ms-transition: all .1s ease-in-out;
  -o-transition: all .1s ease-in-out;
  transition: all .1s ease-in-out, color 0s;
}

.form-label-group textarea::-webkit-input-placeholder,
.form-label-group input::-webkit-input-placeholder {
  color: transparent;
}

.form-label-group textarea:-ms-input-placeholder,
.form-label-group input:-ms-input-placeholder {
  color: transparent;
}

.form-label-group textarea::-ms-input-placeholder,
.form-label-group input::-ms-input-placeholder {
  color: transparent;
}

.form-label-group textarea::-moz-placeholder,
.form-label-group input::-moz-placeholder {
  color: transparent;
}

.form-label-group textarea::placeholder,
.form-label-group input::placeholder {
  color: transparent;
}

.form-label-group textarea:placeholder-shown:focus ~ label,
.form-label-group input:placeholder-shown:focus ~ label {
  color: map_get($theme-colors, primary) !important;
  -webkit-transition: none;
  -moz-transition: none;
  -ms-transition: none;
  -o-transition: none;
  transition: none;
}

.form-label-group textarea:not(:placeholder-shown),
.form-label-group input:not(:placeholder-shown) {
  padding-top: calc(var(--input-padding-y) + var(--input-padding-y) * (2 / 3));
  padding-bottom: calc(var(--input-padding-y) / 3);
}

.form-label-group textarea:not(:placeholder-shown) ~ label,
.form-label-group input:not(:placeholder-shown) ~ label {
  padding-top: calc(var(--input-padding-y) / 3);
  padding-bottom: calc(var(--input-padding-y) / 3);
  font-size: 12px;
}

.form-label-group textarea:not(:placeholder-shown):focus ~ label,
.form-label-group input:not(:placeholder-shown):focus ~ label {
  color: map_get($theme-colors, primary) !important;
}

